const header = document.querySelector('.header');
const hero = document.querySelector('.index-hero');

const logoLight = document.querySelector('.logo__img-light');
const logoDark = document.querySelector('.logo__img-dark');
const headerMenuLink = document.querySelectorAll('.header-menu__link');
const headerMenuTel = document.querySelector('.header-menu__tel');
const burger = document.querySelector('.burger');

const headerHeight = header.offsetHeight;
const heroHeight = hero.offsetHeight - headerHeight;

window.addEventListener('scroll', () => {
   let scrollDistance = window.scrollY;

   if(scrollDistance >= heroHeight) {
      header.classList.add('header_light');
      headerMenuLink.forEach((el) => {
         el.classList.add('header-menu__link_dark');
      })
      headerMenuTel.classList.add('header-menu__tel_dark');
      logoLight.classList.add('logo__img_hidden');
      logoLight.classList.remove('logo__img_visible');
      logoDark.classList.remove('logo__img_hidden');
      logoDark.classList.add('logo__img_visible');
      burger.classList.add('burger_dark');
   } else {
      header.classList.remove('header_light');
      headerMenuLink.forEach((el) => {
         el.classList.remove('header-menu__link_dark');
      })
      headerMenuTel.classList.remove('header-menu__tel_dark');
      logoLight.classList.remove('logo__img_hidden');
      logoLight.classList.add('logo__img_visible');
      logoDark.classList.add('logo__img_hidden');
      logoDark.classList.remove('logo__img_visible');
      burger.classList.remove('burger_dark');
   }
});