class Tabs {
   constructor(selector, options) {
      let defaultOptions = {
         isChanged: () => {}
      }
      this.options = Object.assign(defaultOptions, options);
      
      this.selector = selector;
      this.tabs = document.querySelector(`[data-tabs='${selector}']`);

      if(this.tabs) {
         this.tabList = this.tabs.querySelector('.tabs__nav');
         this.tabsBtns = this.tabs.querySelectorAll('.tabs__nav-btn');
         this.tabsItems = this.tabs.querySelectorAll('.tabs__item'); 
      } else {
         console.error('Selector data-tabs nonexistent');
         return;
      }

      this.check();
      this.init();
      this.events();
   }

   check() {
      if (document.querySelectorAll(`[data-tabs='${this.selector}']`).length > 1) {
         console.error('> 1')
         return;
      }

      if (this.tabsBtns.length !== this.tabsItems.length) {
         console.error('!==')
         return;
      }
   }

   init() {
      this.tabList.setAttribute('role', 'tablist');

      this.tabsBtns.forEach((el, i) => {
         el.setAttribute('role', 'tab');
         el.setAttribute('tabindex', '-1');
         el.setAttribute('id', `${this.selector}${i + 1}`);
         el.classList.remove('.tabs__nav-btn_active');
      });

      this.tabsItems.forEach((el, i) => {
         el.setAttribute('role', 'tabitem');
         el.setAttribute('tabindex', '-1');
         el.setAttribute('aria-labelledby', this.tabsBtns[i].id);
         el.classList.remove('.tabs__item_active')
      });

      this.tabsBtns[0].classList.add('tabs__nav-btn_active');
      this.tabsBtns[0].removeAttribute('tabindex');
      this.tabsBtns[0].setAttribute('aria-selected', 'true');
      this.tabsItems[0].classList.add('tabs__item_active');
   }

   events() {
      this.tabsBtns.forEach((el, i) => {
         el.addEventListener('click', e => {
            let currentTab = this.tabList.querySelector('[aria-selected]');

            if (e.currentTarget !== currentTab) {
               this.switchTabs(e.currentTarget, currentTab);
            }
         });

         el.addEventListener('keydown', e => {
            let index = Array.prototype.indexOf.call(this.tabsBtns, e.currentTarget);
            let dir = null;
   
            if (e.which === 37) {
               dir = index - 1;
            } else if (e.which === 39) {
               dir = index + 1;
            } else if (e.which === 40) {
               dir = 'down';
            } else {
               dir = null;
            }
   
            if (dir !== null) {
               if (dir === 'down') {
                  this.tabsItems[i].focus();
               } else if (this.tabsBtns[dir]) {
                  this.switchTabs(this.tabsBtns[dir], e.currentTarget);
               }
            }
         });
      });
   }

   switchTabs(newTab, oldTab) {
      newTab.focus();
      newTab.removeAttribute('tabindex');
      newTab.setAttribute('aria-selected', 'true');

      oldTab.removeAttribute('aria-selected');
      oldTab.setAttribute('tabindex', '-1');

      let index = Array.prototype.indexOf.call(this.tabsBtns, newTab);
      let oldIndex = Array.prototype.indexOf.call(this.tabsBtns, oldTab);

      this.tabsItems[index].classList.add('tabs__item_active');
      this.tabsItems[oldIndex].classList.remove('tabs__item_active');

      this.tabsBtns[index].classList.add('tabs__nav-btn_active');
      this.tabsBtns[oldIndex].classList.remove('tabs__nav-btn_active');

      this.options.isChanged(this);
   }
}

const newTab = new Tabs('tab');