const header = document.querySelector('.header');
const burger = document.querySelector('.burger');
const menu = document.querySelector('.header-menu');
const menuItem = document.querySelectorAll('.header-menu__item');
const body = document.querySelector('.body');
const main = document.querySelector('.main');

const logoLight = document.querySelector('.logo__img-light');
const logoDark = document.querySelector('.logo__img-dark');


burger.addEventListener('click', () => {
   body.classList.toggle('stop-scroll');
   menu.classList.toggle('active');
   burger.classList.toggle('active');
})

main.addEventListener('click', () => {
   body.classList.remove('stop-scroll');
   menu.classList.remove('active');
   burger.classList.remove('active');

   logoLight.classList.toggle('logo__img_hidden');
   logoLight.classList.toggle('logo__img_visible');
   logoDark.classList.toggle('logo__img_hidden');
   logoDark.classList.toggle('logo__img_visible');
})

menuItem.forEach((el) => {
   el.addEventListener('click', () => {
      menu.classList.remove('active');
      burger.classList.remove('active');

      logoLight.classList.remove('logo__img_hidden');
      logoLight.classList.add('logo__img_visible');
      logoDark.classList.add('logo__img_hidden');
      logoDark.classList.remove('logo__img_visible');
   })
})